import React from 'react';
import newnavi from './images/appnavi-HP/newnavi.png';
import osaka from './images/appnavi-HP/osaka.png';
import FooterMenu from './components/footer.js';


const ProductContent =(props) =>({
render(){

    const PageColor =['black','white','black']
    const PageSequence=['row','row-reverse']


  return(
  <div>
<div style={{backgroundColor:PageColor[props.id],paddingBottom:50,paddingTop:40,height:'35.0vw',display:'flex'}}>
  <div style={{width:'90%',margin:'0 auto',display:'flex',flexDirection:PageSequence[props.id],alignSelf:'center'}}>
  <div style={{width:'50%'}}>
    <div style ={{textAlign:'center'}}>
  <img src={props.name} alt={props.contentTitle} style= {{width:'60%'}}/>
  </div>
  </div>
  <div style={{color:PageColor[props.id + 1], width:'50%',alignSelf:'center',textAlign:'center'}}>
    <div style={{fontSize:'1.8vw'}}>
      <p>{props.release}</p>
      <br></br>
      <p>{props.contentTitle}</p>
      <p>{props.Description}</p>
    </div>
   <div style={{fontSize:'1.0vw'}}>
     <p>{props.Language}</p>
   </div>
   </div>
  </div>
</div>
</div>
)
}
});

const Product  = () => (
<div>
    <h1 style={{color:'white',fontSize:'3.5vw',textAlign:'center',backgroundColor:'black',margin:0,paddingTop:'10.0vw',paddingBottom:'2kk.0vw'}}>Product</h1>
    <ProductContent id = {0} name={newnavi} release="2016/2/4 Release" contentTitle="新入生ナビ" Description="大学生用新歓イベントまとめアプリ" Language="Language : Swift"/>
    <ProductContent id= {1} name={osaka} release="2018/3/22 Develop" congtentTitle="大阪城" Description="大阪城観光VR" Language="Made by Unity"/>
</div>
)

const ProductPage =()=> (
  <div>
    <Product/>
    <FooterMenu/>
  </div>
)
export  {Product,ProductPage};
