import React from 'react';
import contact from './images/appnavi-HP/contact.png';
import FooterMenu from './components/footer.js';

const Contact  = () => (
<div style={{backgroundColor:'black'}}>
  <div style={{width:'90%',margin:'0 auto',paddingBottom:'2.0vw'}}>
  <h1 style={{color:'white' ,textAlign:'center', paddingTop:'10.0vw',paddingBottom:'6.0vw' ,margin:0,fontSize:'3.0vw'}}>Contact us</h1>
  <div style={{display:'flex',justifyContent:'space-around'}}>
  <div style={{width:'60%'}}>
  <img src={contact} alt="contact" style= {{width:'90%'}}/>
  </div>
  <div style={{color:'white' ,width:'40%',alignSelf:'center',fontSize:'2.0vw'}}>
    <p style={{fontFamily:'ヒラギノ角ゴ Pro,sans-serif'}}>
      お仕事依頼、質問、部活に関することなど、
    以下までお問い合わせください</p>
  <p>Conatact us : contact@appnavi.com</p>
  <div style={{display:'flex',justifyContent:'space-around',width:'60%'}}>
    <div>
      <p>Twitter</p>
      <i className="fab fa-twitter fa-3x" ></i>
    </div>
    <div>
      <p>LINE</p>
      <i className="fab fa-line fa-3x" ></i>
    </div>
</div>
</div>
  </div>

  </div>
</div>
)

const ContactPage =()=> (
  <div>
  <Contact/>
  <FooterMenu/>
  </div>
)

export {Contact,ContactPage};
